*, *:after, *:before { -webkit-box-sizing:border-box; -moz-box-sizing:border-box; box-sizing:border-box;}
html, body { height:100%;  margin:0; padding:0; font-family:"Playfair Display"; font-weight:400;  }
body { overflow-x:hidden !important;  }
h1, h2, h3, h4, h5, h6 { font-family:"Playfair Display";  padding:0; margin:0; font-weight:600; margin-bottom:10px; color:#414141; }
p, li  { line-height:30px; font-size:15px; color:#666; font-family:"Heebo"; }
a { color:#666; -webkit-transition:all .2s ease-in-out; -moz-transition:all .2s ease-in-out; transition:all .2s ease-in-out; text-decoration:none; }
a:hover,a:focus { text-decoration:none; outline:0;  } 
.clear { clear:both; float:none !important; width:100% !important; }
.ie6,.ie7,.ie8,.ie9, img{ border:none; }

.btn {padding:6px 20px;border-radius:0px;font-size:18px;}.btn-border {font-size:16px;border:2px solid#5cddf2;color:#000;text-transform:uppercase;}.btn:focus {outline:none;box-shadow:none;}section {padding:50px 0 50px 0;}.relative {position:relative;}.white {color:#fff;}.red {color:#d93025;}.orange {color:#f16638;}.anchor {color:#007bff;}.bold {font-weight:600;}.extra_bold {font-weight:900;}strong {font-weight:500;}.padding-0 {padding:0px !important;}.italic {font-style:italic;}.box_shadow {box-shadow: 0px 0px 2.7em 0px rgba( 0, 0, 0, 0.08 );}.title_head {color:#f16638;}

/*------------ global styling for title -----------------*/
.entry-title{text-align: center;padding: 40px 0;}.entry-title h2{color:#f16638;}

/* Error */
.error { position:absolute; margin-top:-12px; }

/* Hover Sweep To Right */
.sweep-to-right {vertical-align: middle;-webkit-transform: perspective(1px) translateZ(0);transform: perspective(1px) translateZ(0);box-shadow: 0 0 1px rgba(0, 0, 0, 0);position: relative;-webkit-transition-property: color;transition-property: color;-webkit-transition-duration: 0.3s;transition-duration: 0.3s;}.sweep-to-right:before {content: "";position: absolute;z-index: -1;top: 0;left: 0;right: 0;bottom: 0;background: #414141;-webkit-transform: scaleX(0);transform: scaleX(0);-webkit-transform-origin: 0 50%;transform-origin: 0 50%;-webkit-transition-property: transform;transition-property: transform;-webkit-transition-duration: 0.3s;transition-duration: 0.3s;-webkit-transition-timing-function: ease-out;transition-timing-function: ease-out;}.sweep-to-right:hover, .hvr-sweep-to-right:focus, .hvr-sweep-to-right:active {color: #fff !important;}.sweep-to-right:hover:before, .hvr-sweep-to-right:focus:before, .hvr-sweep-to-right:active:before {-webkit-transform: scaleX(1);transform: scaleX(1);}

/* Shutter In Horizontal */
.shutter-horizontal {display: inline-block;vertical-align: middle;-webkit-transform: perspective(1px) translateZ(0);transform: perspective(1px) translateZ(0);box-shadow: 0 0 1px rgba(0, 0, 0, 0);position: relative;background: #414141;-webkit-transition-property: color;transition-property: color;-webkit-transition-duration: 0.3s;transition-duration: 0.3s;}.shutter-horizontal:before {content: "";position: absolute;z-index: -1;top: 0;bottom: 0;left: 0;right: 0;background:#fff;-webkit-transform: scaleX(1);transform: scaleX(1);-webkit-transform-origin: 50%;transform-origin: 50%;-webkit-transition-property: transform;transition-property: transform;-webkit-transition-duration: 0.3s;transition-duration: 0.3s;-webkit-transition-timing-function: ease-out;transition-timing-function: ease-out;}.shutter-horizontal:hover, .shutter-horizontal:focus, .shutter-horizontal:active {color: white;}.shutter-horizontal:hover:before, .shutter-horizontal:focus:before, .shutter-horizontal:active:before {-webkit-transform: scaleX(0);transform: scaleX(0);}

/* Selection */
::-moz-selection{background:#414141;color:#fff;}::selection {background:#414141;color:#fff;}.btn {font-family:"Heebo";font-size:16px;color:#333;border-radius:24px;padding:12px 30px;}.btn-theme {background:#f16638;color:#ffffff;}.btn-theme:hover {background:#f16638;color:#ffffff;}.btn img {width:20px;margin-left:6px;}#pagetitle {text-transform:capitalize;}.modal-backdrop.show {opacity: .9;}

/* ===== Page Preload ===== */
#preloader { position: fixed; top:0; left:0; right:0; bottom:0; background:#fff; z-index:9999; }
#status { background:url(../img/ajax-loader.gif) no-repeat; background-size:cover; position:absolute; top:50%; transform:translateY(-50%); width:120px; height:120px; left:0px; right:0px; z-index:999; margin:0 auto; }
.heading { font-size:48px; margin-bottom:40px; /*text-transform:uppercase;*/ }

/* Scrollup ==============*/
#scrollUp { background:#d8ad48 none repeat scroll 0 0; border-radius: 100%; bottom: 5px; color: #fff; font-size:16px; height: 40px; right:110px; text-align:center; text-decoration:none; width:40px; display:none !important; }
#scrollUp:hover { opacity: 1; color:#fff!important; background:#d8ad48; }
#scrollUp i { display: block; font-weight: bold; padding-top: 11px; }

/* Header */

.top_header { background:rgb(0 0 0 / 45%); width:100%; position:absolute; top:0px; left:0px; padding:4px 0; z-index:999; }
.top_header p { color:#fff; margin-bottom:0px; font-size:12px; line-height:24px;  }
.top_header a {/* background:#fff; */ color:#fff; font-size:13px; padding:2px 0; width:30px; display:inline-block; text-align:center; border-radius:2px; margin-right:2px; }

.navbar {width:100%;margin:0 auto;left:0px;right:0px;top:0px;background:none;padding:44px 0px 6px 0px !important;-webkit-transition: all .2s ease-in-out;-moz-transition: all .2s ease-in-out;transition: all .2s ease-in-out;}.navbar-nav>li {margin-left:12px;margin-right:0px !important;}.navbar-nav>li.contactus a {background:#897ab9;color:#fff;}.navbar-nav>li>a {color:#666;display:block;line-height:normal !important;font-size:16px !important;padding:7px 5px;border-bottom:4px solid transparent;transition:all 0.4s ease 0s;position:relative;}.navbar .navbar-nav>li>a.active, .navbar .navbar-nav>li>a:focus, .navbar .navbar-nav>li>a:hover, .navbar .navbar-nav>li>a.active {border-bottom:4px solid#f16638;}.logo {width:65px;padding:0px;display: block;}.navbar .logo_light {display:none;}.navbar .logo_dark {display:block;}.navbar-shrink .jewels_light {display:none;}.navbar-shrink .jewels_dark {display:block;}

.header_menu_area.navbar_fixed .navbar.navbar-expand-lg {padding-top:40px !important;}.header_menu_area.navbar_fixed .top_header {top:-2px;}.header_menu_area.navbar_fixed .top_header p {font-size:12px;}.header_menu_area .top_header a{width: auto;display: inline-block;margin: 0;padding: 4px 20px;border-radius: 10px;line-height: 1;text-transform: capitalize;font-family:"Heebo";font-weight: 500;font-size: 12px;color: #fff;}.header_menu_area .top_header a span{margin-left: 10px;}.header_menu_area.white_menu.navbar_fixed {z-index:9999 !Important;}.navbar-shrink {background:#fff;width:100%;top:0px;z-index:999;padding:50px 60px 6px 60px;border:none;-webkit-transition: all .2s ease-in-out;-moz-transition: all .2s ease-in-out;transition: all .2s ease-in-out;moz-box-shadow: 0 2px 5px rgba(16,16,16,.08);-webkit-box-shadow: 0 2px 5px rgba(16,16,16,.08);box-shadow: 0 2px 5px rgba(16,16,16,.08);}.navbar-shrink .top_header {background:#cf2e2e;}.navbar-shrink li a {color:#333;}.header-call p {border-left:1px solid#ddd;padding-left:15px;margin-bottom:0px;}.header-call p img {width:50px;}.header-call a {color:#666;display:inline-block;}

.nav_height { height:106px; }
 
@media (max-width:993px) {
.full_header.mobile_menu.navbar_fixed { top:-72px;display: flex; justify-content: space-between;align-items: center;}    
.full_header .float-right .bar_menu,
.side_menu .close-menu { background:transparent; }

.menu-is-opened .full_header .float-right .bar_menu, .menu-is-opened .side_menu .close-menu{
    background: #f16638;
    display: flex;
    align-items: center;
    justify-content: center;
}

.side_menu .menu-list  { max-height:60vh; margin-top: 0;}
.side_menu .copy  { font-size:14px; } 
}

